import React from "react";

import Navbar from "../../components/NavBar";

import classes from "./PrivacyPolicy.module.css";

const Privacy = () => {
  return (
    <div className={classes.container}>
      <Navbar />
      <h1 className={classes.subheader}>Privacy Policy for Locallearn</h1>
      <h3 className={classes.subHeading}>Your Privacy is important to us</h3>
      <p className={classes.content}>
        We take protecting your privacy seriously, and we want you to understand
        exactly how and why we use your information. This document is our
        Privacy Policy, which describes what information we collect and what
        it's used for. It applies to all interactions with Locallearn.in.
        However, other services we operate - such as myty apps - have their own
        terms, and you should review them on each store's respective website.
        <br />
        Please read this Privacy Policy carefully. Your use of and/or
        registration with Locallearn will constitute your agreement to our
        policy. If you cannot agree, please do not use our services.
        Additionally, our Privacy Policy is part of our User Agreement, and by
        using Locallearn you agree to both.
      </p>
      <h3 className={classes.subHeading}>About this policy</h3>
      <p className={classes.content}>
        Locallearn is an open platform that allows you to chat about anything
        you want on any topic you want. In future this platform will allow you
        to connect with co-learners to help you ace your learning journey. If
        you register we collect some information for your account you, and other
        information that is necessary to make the service work. Our goal in
        developing our privacy practices is to ensure safety and protection of
        your data and we don't sell or profit from the information you share
        with us.
        <br />
        We do take efforts to make sure the platform keeps working. This
        includes using your information, where necessary, to stop spam and
        gaming and other activities that violate the user agreement. Please read
        this Privacy Policy carefully. It describes what information we collect,
        how it is used, and when it is shared so you can make smart decisions
        about how you use Locallearn chatbot! We think it strikes a good balance
        and respects you - our users - who make this whole thing possible. Let
        us know what you think.
      </p>

      <h3 className={classes.subHeading}>Information we collect</h3>
      <p className={classes.content}>
        When you create an account, you are required to provide a username and
        password, and a valid email address or phone number. Name- We collect
        this information to enable Locallearn chatbot to personalise your
        conversation by addressing you by name. Email/ phone number- To enable
        you to log in using your email/ phone and restore your password in case
        you forget it
        <br />
        Chat conversation with Locallearn bot-
        <br />
        Messages you sent to Locallearn, to improve bots responses and quality
        of your chat experience.
        <br />
        Details like your hobbies, interests, skills etc., so that bot helps you
        find better learning connect for you and also connect with you to
        potential co-learners (in future)
        <br />
        We also log, and retain the IP address from which the account is
        initially created.
        <br />
        We collect and store your usage data, such as button clicks or search
        queries, to track and analyze product metrics and to better understand
        and serve the users of the Services.
      </p>

      <h3 className={classes.subHeading}>Using or disclosing information</h3>
      <p className={classes.content}>
        It takes a lot of work to keep our site running and prevent abuse, and
        we sometimes use your data in order to facilitate this. Your information
        is used internally only where necessary to provide our services. Your
        private information is never for sale.
        <br />
        Our use of your information is limited to improving your experience of
        the Locallearn chatbot, and promoting its appeal to a wider public. We
        may choose to display excerpts of your conversations, or of messages you
        post, on the site, in promotional material, or in other media such as
        social networks. If we do so we will ensure that you will not be
        personally identifiable, editing if appropriate.
        <br />
        For the purposes of complying with legislative requirements, court acts
        or other public authorities instructions, we may need to access, read,
        preserve, and disclose any information; we can do that if we reasonably
        find it necessary to apply our Terms of service and other agreements; or
        protect the rights, property, or safety of Locallearn, DOIONS, our
        employees, our users, or others. Subject to applicable law, you will be
        duly notified of such processing activities.We do not sell or otherwise
        give access to any information about identifiable users to any third
        party.
      </p>

      <h3 className={classes.subHeading}>Cookie Policy</h3>
      <p className={classes.content}>
        Cookies are small data files placed on your computer by websites you
        visit in order to help them remember certain information about you.We
        may use cookies and other tracking technologies to collect and store
        your information.
        <br />
        We may use cookies and similar tracking technologies (like web beacons
        and pixels) to access or store information. Specific information about
        how we use such technologies and how you can refuse certain cookies is
        set out in our Cookie Notice.
      </p>

      <h3 className={classes.subHeading}>Data storage</h3>
      <p className={classes.content}>
        We store all your data on a secure Cloud server which is fully
        encrypted. We take all steps reasonably necessary to ensure that your
        data is treated securely and in accordance with this Privacy Policy. If
        you are located in another jurisdiction, please be aware that the
        information you provide to us may be transferred to, stored and
        processed in India.
      </p>

      <h3 className={classes.subHeading}>Data retention</h3>
      <p className={classes.content}>
        We will only retain your personal information for as long as necessary
        to fulfill the purposes we collected it for, including for the purposes
        of satisfying any legal, accounting, or reporting requirements.
        <br />
        satisfying any legal, accounting, or reporting requirements. To
        determine the appropriate retention period for personal information, we
        consider the amount, nature, and sensitivity of the personal
        information, the potential risk of harm from unauthorized use or
        disclosure of your personal information, the purposes for which we
        process your personal information and whether we can achieve those
        purposes through other means, and the applicable legal requirements.
      </p>

      <h3 className={classes.subHeading}>Analytics, Security and Age</h3>
      <p className={classes.content}>
        We use a tool called Google Analytics to help us understand how users
        interact with our website. It tells us aggregate usage and traffic
        information, such as how the site was accessed, how long visitors
        stayed, and the number of first-time visitors to the site. By knowing
        how people use the site, we can make it better.
        <br />
        Locallearn will make every effort to secure any private information
        submitted to us by our users. However, no data transmission over the
        internet is completely secure, so we cannot guarantee the absolute
        security of this data. You use the service at your own risk, and are
        responsible for taking reasonable measures to secure your account (such
        as carefully maintaining the secrecy of your password).
        <br />
        Although we welcome users from all walks of life, Locallearn is not
        aimed at children. Individuals under the age of 13 may not create an
        account with us. If you believe someone 12 or younger is using our site
        without parental consent, please contact us at
        <a href="#">care@myty.in</a> .
      </p>

      <h3 className={classes.subHeading}>Social media</h3>
      <p className={classes.content}>
        Locallearn allows you to post to social media, including Facebook,
        Instagram or Twitter, but Locallearn will not connect to the servers of
        those services, share information with those services, or post on your
        behalf. We want to make it easier for you to share your favourite
        Locallearn content elsewhere, but we want you to control when and how
        that occurs.
      </p>

      <h3 className={classes.subHeading}>Changes to Privacy Policy</h3>
      <p className={classes.content}>
        The Services and our business may change from time to time. As a result,
        at times it may be necessary for us to make changes to this Privacy
        Policy. We reserve the right to update or modify this Privacy Policy at
        any time and from time to time without prior notice. We encourage you to
        periodically review this page for the latest information on our privacy
        practices. Your continued use of the Services after any changes or
        revisions to this Privacy Policy shall indicate your agreement with the
        terms of such revised Privacy Policy.
      </p>
    </div>
  );
};

export default Privacy;
